@import "../../global.scss";
.grid{
        
        display: grid;
        position: fixed;
        width: 100%;
        height: 100%;
        text-align: center;
        justify-content: center;
        margin-top: 30px;
        margin-bottom: 30px;
      
      

        .cell{
            
            border:1px solid gray;
        }

}
.settings{
    cursor: pointer;
    height: 45px;
    color: black;
    font-size: 35px;
    background-color: red;
    overflow: hidden;
    border-radius: 10px;
    position: absolute;
    left: 0;
    top: 0;

    @include mobile{
      left: 45px;
      transform: rotate(90deg);
      transform-origin: top left
      
      
    }
    
    
  }

  .start{
    cursor: pointer;
    height: 45px;
    color: black;
    font-size: 35px;
    background-color: green;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
    border-radius: 10px;
      @include mobile{
          right: 45px;
          transform: rotate(-90deg);
          transform-origin: top right;
        
      }
  }
